// src/services/claudeAPI.js
import axios from "axios";
import { CLAUDE_CONFIG, ERROR_MESSAGES } from "@config/constants";

class ClaudeAPIService {
  constructor() {
    // Use your Worker URL from .env (recommended) or hardcode it while testing
    this.baseURL = import.meta.env.VITE_API_BASE_URL || CLAUDE_CONFIG.baseURL;

    this.client = axios.create({
      baseURL: this.baseURL, // <-- Cloudflare Worker, not Anthropic
      headers: { "Content-Type": "application/json" },
      timeout: 60000,
    });
  }

  // On the frontend we never keep a real API key; the worker has it
  isConfigured() {
    return Boolean(this.baseURL);
  }

  async sendMessage(prompt, options = {}) {
    if (!this.isConfigured()) {
      throw new Error(ERROR_MESSAGES.API_ERROR);
    }

    try {
      const { data } = await this.client.post("/messages", {
        model: CLAUDE_CONFIG.model,
        max_tokens: options.maxTokens || CLAUDE_CONFIG.maxTokens,
        temperature: options.temperature || CLAUDE_CONFIG.temperature,
        messages: [{ role: "user", content: prompt }],
      });

      // Anthropic returns { content: [{ text: "..."}], ... }
      return data?.content?.[0]?.text ?? "";
    } catch (error) {
      if (error.response) {
        const status = error.response.status;
        const message = error.response.data?.error?.message || "Unknown error";
        if (status === 401) throw new Error("Invalid proxy or key.");
        if (status === 429) throw new Error("Rate limit exceeded. Try later.");
        if (status >= 500) throw new Error("Claude API unavailable. Try later.");
        throw new Error(`API Error: ${message}`);
      } else if (error.request) {
        throw new Error(ERROR_MESSAGES.NETWORK_ERROR);
      } else {
        throw new Error(ERROR_MESSAGES.API_ERROR);
      }
    }
  }

  async analyzeResume(resumeText, jobDescription) {
    const prompt = /* same prompt you already have */ `
You are an expert resume analyzer and career coach. Analyze the following resume against the job description and provide detailed insights.

RESUME:
${resumeText}

JOB DESCRIPTION:
${jobDescription}

Please provide a comprehensive analysis in the following JSON format:
{ ...same JSON schema... }

IMPORTANT:
- Be specific and actionable
- Include actual keywords from the job description
- Provide concrete examples
- Focus on ATS optimization
- Response must be valid JSON only, no markdown or explanations`;

    const response = await this.sendMessage(prompt, {
      maxTokens: 4096,
      temperature: 0.3,
    });

    // Parse JSON-only response
    const cleaned = response.trim().replace(/```json\n?|```\n?/g, "");
    return JSON.parse(cleaned);
  }

  async enhanceBulletPoints(originalBullets, jobKeywords) {
    const prompt = `You are an expert resume writer...`; // keep your prompt
    const response = await this.sendMessage(prompt, { temperature: 0.7 });
    const cleaned = response.trim().replace(/```json\n?|```\n?/g, "");
    try { return JSON.parse(cleaned); } catch { return []; }
  }

  async generateSummary(resumeText, jobDescription) {
    const prompt = `Based on this resume and job description, create a compelling professional summary...`;
    return await this.sendMessage(prompt, { maxTokens: 500, temperature: 0.7 });
  }

  async getCareerAdvice(resumeText, targetRole) {
    const prompt = `As a career advisor, provide personalized advice...`;
    return await this.sendMessage(prompt, { maxTokens: 1000, temperature: 0.8 });
  }
}

export const claudeAPI = new ClaudeAPIService();
export default ClaudeAPIService;
